@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600&display=swap);
:root {
    --header-height: 3rem;
    --font-medium: 500
}

:root {
    --first-color: #1267d6;
    --white-color: #FAFAFF;
    --dark-color: #091216;
    --text-color: #697477;
}

:root {
    --body-font: 'Monserrat', sans-serif;
    --big-font-size: 6.25rem;
    --h2-font-size: 1.25rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
}

@media screen and (min-width:768px) {
     :root {
        --big-font-size: 10.25rem;
        --h2-font-size: 2rem;
        --normal-font-size: 1rem;
        --small-font-size: 0.875rem;
    }
}


/*===========Margenes =======*/

:root {
    --mb-1: 0.5rem;
    --mb-2: 1rem;
    --mb-3: 1.5rem;
    --mb-4: 2rem;
}


/*=========== z index =======*/

:root {
    --z-fixed: 100;
}


/*=========== BASE =======*/

*,
::before,
::after {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    margin: 3rem 0 0 0;
    margin: var(--header-height) 0 0 0;
    font-family: 'Monserrat', sans-serif;
    font-family: var(--body-font);
    font-size: 0.938rem;
    font-size: var(--normal-font-size);
    font-weight: 500;
    font-weight: var(--font-medium);
    color: #697477;
    color: var(--text-color);
    line-height: 1.6;
}

h1,
h2,
p {
    margin: 0;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    text-decoration: none;
    color: #697477;
    color: var(--text-color);
}

img {
    max-width: 100%;
    height: auto;
    display: block;
}


/*=========== CLASS CSS =======*/

.section {
    padding: 3rem 0;
}

.section-title {
    position: relative;
    font-size: 1.25rem;
    font-size: var(--h2-font-size);
    color: #091216;
    color: var(--dark-color);
    margin: 2rem 0;
    margin: var(--mb-4) 0;
    text-align: center;
}

.section-title::after {
    position: absolute;
    content: '';
    width: 32px;
    height: 0.18rem;
    left: 0;
    right: 0;
    margin: auto;
    top: 2rem;
    background-color: #1267d6;
    background-color: var(--first-color);
}


/*=========== LAYOUT =======*/

.bg-grid {
    max-width: 1024px;
    display: grid;
    grid-template-columns: 100%;
    grid-column-gap: 2rem;
    width: calc(100% -2rem);
    margin-left: 1rem;
    margin-left: var(--mb-2);
    margin-right: 1rem;
    margin-right: var(--mb-2);
}

.l-header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    z-index: var(--z-fixed);
    background-color: #1267d6;
    background-color: var(--first-color);
}


/*=========== NAV =======*/

.nav {
    height: 3rem;
    height: var(--header-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (max-width:767px) {
    .nav__menu {
        position: fixed;
        top: 3rem;
        top: var(--header-height);
        right: -100%;
        width: 80%;
        height: 100%;
        padding: 2rem;
        background-color: rgba(255, 255, 255, .3);
        transition: 0.5s;
        -webkit-backdrop-filter: blur(10px);
                backdrop-filter: blur(10px);
    }
}

.nav__item {
    margin-bottom: 2rem;
    margin-bottom: var(--mb-4);
}

.nav__link {
    position: relative;
    color: #091216;
    color: var(--dark-color);
}

.nav__link:hover {
    color: #1267d6;
    color: var(--first-color);
}

.nav__logo {
    color: #FAFAFF;
    color: var(--white-color);
    padding-left: 1rem;
}

.nav__toggle {
    color: #FAFAFF;
    color: var(--white-color);
    font-size: 1.5rem;
    cursor: pointer;
}


/*=========== SHOW MENU =======*/

.show {
    right: 0;
}


/*=========== Active Menu =======*/

.active::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 0.18rem;
    left: 0;
    top: 2rem;
    background-color: #1267d6;
    background-color: var(--first-color);
}


/*=========== Home =======*/

.home {
    position: relative;
    background-color: #1267d6;
    background-color: var(--first-color);
    overflow: hidden;
}

.home__container {
    height: calc(100vh - 3rem);
    height: calc(100vh - var(--header-height));
    grid-row-gap: 5rem;
    row-gap: 5rem;
}

.home__title {
    align-self: flex-end;
    font-size: 6.25rem;
    font-size: var(--big-font-size);
    color: #FAFAFF;
    color: var(--white-color);
    line-height: .8;
}

.home__title span {
    text-shadow: 0 20x 25px rgba(0, 0, 0, .5);
}

.home_green {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: green;
}

.home__scroll {
    align-self: flex-end;
    padding-bottom: 2rem;
    padding-bottom: var(--mb-4);
}

.home__scroll-link {
    -webkit-writing-mode: vertical-lr;
        -ms-writing-mode: tb-lr;
            writing-mode: vertical-lr;
    transform: rotate(-180deg);
    color: #FAFAFF;
    color: var(--white-color);
}

.home__img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 295px;
}


/*=========== About =======*/

.about__container {
    justify-items: center;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    text-align: center;
}

.about__img {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
    width: 120px;
    height: 120px;
    background-color: #1267d6;
    background-color: var(--first-color);
    border-radius: 50%;
    overflow: hidden;
}

.about__img img {
    width: 100px;
}

.about__subtitle {
    font-size: 1.25rem;
    font-size: var(--h2-font-size);
    color: #1267d6;
    color: var(--first-color);
    margin-bottom: 0.5rem;
    margin-bottom: var(--mb-1);
}

.about__text {
    margin-bottom: 2rem;
    margin-bottom: var(--mb-4);
}

.about__profession {
    display: block;
    margin-bottom: 2rem;
    margin-bottom: var(--mb-4);
}

.about__social-icon {
    font-size: 1.4rem;
    margin: 0 0.5rem;
    margin: 0 var(--mb-1);
}

.about__social-icon:hover {
    color: #1267d6;
    color: var(--first-color);
}


/*=========== Skills =======*/

.skills__container {
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.skills__subtitle {
    color: #1267d6;
    color: var(--first-color);
    margin-bottom: 1.5rem;
    margin-bottom: var(--mb-3);
}

.skills__name {
    display: inline-block;
    font-size: 0.813rem;
    font-size: var(--small-font-size);
    margin-right: 1rem;
    margin-right: var(--mb-2);
    margin-bottom: 1.5rem;
    margin-bottom: var(--mb-3);
    padding: .25rem;
    background-color: #FAFAFF;
    background-color: var(--white-color);
    border-radius: .25rem;
}

.skills__name:hover {
    background-color: #1267d6;
    background-color: var(--first-color);
    color: #FAFAFF;
    color: var(--white-color);
}

.skills__img img {
    border-radius: .5rem;
}


/*=========== Portfolio =======*/

.portfolio {
    background-color: #FAFAFF;
    background-color: var(--white-color);
}

.portfolio__container {
    justify-items: center;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.portfolio__img {
    position: relative;
    overflow: hidden;
}

.portfolio__img img {
    border-radius: .5rem;
}

.portfolio__link {
    position: absolute;
    bottom: -100%;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: .5rem;
    cursor: pointer;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    transition: .3s;
    display: flex;
    align-items: flex-start;
}

.portfolio__img:hover .portfolio__link {
    bottom: 0;
}

.portfolio__link-name {
    margin-top: 25%;
    background-color: #1267d6;
    background-color: var(--first-color);
    color: #FAFAFF;
    color: var(--white-color);
    padding: 0.75rem 2.5rem;
    border-radius: 0.5rem;
    border: none;
    outline: none;
    font-size: 0.938rem;
    font-size: var(--normal-font-size);
    cursor: pointer;
}


/*=========== Contact =======*/

.contact__container {
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.contact__subtitle {
    font-size: 0.938rem;
    font-size: var(--normal-font-size);
    color: #1267d6;
    color: var(--first-color);
}

.contact__text {
    display: inline-block;
    margin-bottom: 1rem;
    margin-bottom: var(--mb-2);
}

.contact__inputs {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}

.contact__input {
    width: 100%;
    padding: .8rem;
    outline: none;
    border: 1.5px solid #091216;
    border: 1.5px solid var(--dark-color);
    font-size: 0.938rem;
    font-size: var(--normal-font-size);
    margin-bottom: 2rem;
    margin-bottom: var(--mb-4);
    border-radius: .5rem;
}

.contact__button {
    display: block;
    background-color: #1267d6;
    background-color: var(--first-color);
    color: #FAFAFF;
    color: var(--white-color);
    padding: 0.75rem 2.5rem;
    margin-left: auto;
    border-radius: 0.5rem;
    border: none;
    outline: none;
    font-size: 0.938rem;
    font-size: var(--normal-font-size);
    cursor: pointer;
}


/*=========== Contact =======*/

.footer {
    background-color: #091216;
    background-color: var(--dark-color);
}

.footer__container {
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.footer__title {
    font-size: 0.938rem;
    font-size: var(--normal-font-size);
    color: #FAFAFF;
    color: var(--white-color);
    margin-bottom: 1rem;
    margin-bottom: var(--mb-2);
}

.footer__link {
    padding: .25rem 0;
}

.footer__link:hover {
    color: #1267d6;
    color: var(--first-color);
}

.footer__social {
    font-size: 1.4rem;
    margin-right: 0.5rem;
    margin-right: var(--mb-1);
}

.footer__social:hover {
    color: #1267d6;
    color: var(--first-color);
}


/* Media queries */

@media screen and (min-width:768px) {
    body {
        margin: 0;
    }
    .section {
        padding-top: 4rem;
    }
    .section-title {
        margin-bottom: 3rem;
    }
    .section-title::after {
        width: 64px;
        top: 3rem;
    }
    .nav {
        height: calc(3rem + 1rem);
        height: calc(var(--header-height) + 1rem);
        width: 80%;
        margin-left: auto;
        margin-right: auto;
    }
    .nav__list {
        display: flex;
    }
    .nav__item {
        margin-left: 2rem;
        margin-left: var(--mb-4);
        margin-bottom: 0;
    }
    .nav__toggle {
        display: none;
    }
    .nav__link {
        color: #FAFAFF;
        color: var(--white-color);
    }
    .nav__link:hover {
        color: #FAFAFF;
        color: var(--white-color);
    }
    .active::after {
        background-color: #FAFAFF;
        background-color: var(--white-color);
    }
    .home__container {
        height: 100vh;
        grid-template-rows: 1.7fr 1fr;
        grid-row-gap: 0;
        row-gap: 0;
    }
    .home__img {
        width: 524px;
        right: 7%;
    }
    .about__container {
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
        text-align: left;
        text-align: initial;
        padding: 4rem 0;
        height: 100vh;
    }
    .about__img {
        width: 200px;
        height: 200px;
    }
    .about__img img {
        width: 165px;
    }
    .skills__container {
        grid-template-columns: repeat(2, 1fr);
    }
    .portfolio__container {
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 2rem;
        -webkit-column-gap: 2rem;
                column-gap: 2rem;
    }
    .contact__container {
        grid-template-columns: repeat(2, 1fr);
        justify-items: center;
    }
    .contact__form {
        width: 380px;
    }
    .footer__container {
        grid-template-columns: repeat(3, 1fr);
        justify-items: center;
    }
}

@media screen and (min-width:1024px) {
    .bg-grid {
        margin-left: auto;
        margin-right: auto;
    }
    .about__container,
    .skills__container,
    .portfolio__container {
        height: calc(100vh - 3rem);
        height: calc(100vh - var(--header-height));
        align-items: initial;
    }
}
